import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { ApplicationService } from "./application.service";

@Component({
	selector: "app-review-applications",
	templateUrl: "./review-applications.component.html",
	styleUrls: ["./review-applications.component.css"],
})
export class ReviewApplicationsComponent implements OnInit {
	isLoading: boolean;
	applicationDataAll: any;
	applicationDataRejected: any;
	applicationDataonHold: any;
	allApplicationCount: number = 0;
	RejectCount: number = 0;
	onHoldCount: number = 0;
	challengeAnchorId: any;
	queryAll: any = {
		filter: {
			status: "active",
			application_status: "New",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	queryRejected: any = {
		filter: {
			status: "active",
			application_status: "Reject",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	queryHold: any = {
		filter: {
			status: "active",
			application_status: "Onhold",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	constructor(private _app: ApplicationService, private route: ActivatedRoute) {
		this.route.parent.params.subscribe((params) => (this.challengeAnchorId = params.id));
		this.queryAll.filter.frg_anchor_id = this.challengeAnchorId;
		this.queryRejected.filter.frg_anchor_id = this.challengeAnchorId;
		this.queryHold.filter.frg_anchor_id = this.challengeAnchorId;
	}

	ngOnInit() {
		this.fetchAllApplications();
	}

	async fetchAllApplications() {
		this.isLoading = true;
		this.applicationDataAll = await this._app.getApplications(this.queryAll);
		this.allApplicationCount = this.applicationDataAll.total_results;
		// console.log(this.applicationDataAll);
		this.applicationDataRejected = await this._app.getApplications(this.queryRejected);
		this.RejectCount = this.applicationDataRejected.total_results;
		this.applicationDataonHold = await this._app.getApplications(this.queryHold);
		this.onHoldCount = this.applicationDataonHold.total_results;
		this.isLoading = false;
	}

	handleStatusChange(resp) {
		this.fetchAllApplications();
	}

	sortNewApplication(clause) {
		this.queryAll.sort = clause.target.value;
		this.fetchAllApplications();
	}

	sortOnhold(clause) {
		this.queryHold.sort = clause.target.value;
		this.fetchAllApplications();
	}

	sortRejected(clause) {
		this.queryRejected.sort = clause.target.value;
		this.fetchAllApplications();
	}

	handleApplicationAll(pageIndex) {
		this.queryAll.pg.skip = (pageIndex - 1) * this.queryAll.pg.limit;
		this.fetchAllApplications();
	}
	handleApplicationOnhold(pageIndex) {
		this.queryHold.pg.skip = (pageIndex - 1) * this.queryHold.pg.limit;
		this.fetchAllApplications();
	}
	handleApplicationReject(pageIndex) {
		this.queryRejected.pg.skip = (pageIndex - 1) * this.queryRejected.pg.limit;
		this.fetchAllApplications();
	}
}
