import { Component, Input, OnChanges, OnInit } from "@angular/core";
import { DashboardmainService } from "src/app/seeker/challenge-dashboard/dashboardmain.service";
import * as moment from "moment";

@Component({
	selector: "app-overview-proposals",
	templateUrl: "./overview-proposals.component.html",
	styleUrls: ["./overview-proposals.component.css"],
})
export class OverviewProposalsComponent implements OnChanges {
	@Input() anchorId: any;
	queryAll: any = {
		filter: {
			status: "active",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	proposalData: any;
	loading: boolean;
	constructor(private _dash: DashboardmainService) {}

	async ngOnChanges() {
		this.loading = true;
		// console.log("Anchor id", this.anchorId);
		if (this.anchorId != undefined) {
			await this.fetchProposals();
		}
		this.loading = false;
	}
	async fetchProposals() {
		this.queryAll.filter.frg_anchor_id = this.anchorId;
		this.proposalData = await this._dash.getProposals(this.queryAll);
		// console.log("ProposalData", this.proposalData);
		await this.proposalData.data.map((app) => {
			app.daysago = moment(app.created_at).fromNow();
		});
	}
}
