import { Component, Input, OnInit } from "@angular/core";
import { AnchorWidgetServiceService } from "src/app/challenge/components/anchor-widget/anchor-widget-service.service";

@Component({
	selector: "app-overview-anchor",
	templateUrl: "./overview-anchor.component.html",
	styleUrls: ["./overview-anchor.component.css"],
})
export class OverviewAnchorComponent implements OnInit {
	@Input() anchorId: number;
	@Input() challengeId: number;
	widgetLoading: boolean;
	anchorData: any;
	constructor(private _anchor: AnchorWidgetServiceService) {}

	async ngOnInit() {
		await this.fetchAnchors();
	}

	async fetchAnchors() {
		this.widgetLoading = true;
		let resp: any = await this._anchor.getAnchorsForChallenge(this.challengeId);
		this.anchorData = resp.data.filter((e) => {
			if (e.id != this.anchorId) return e;
		});
		this.anchorData.map((anchors) => {
			let smartcityId = anchors.frg_seeker_id.frg_smart_city_id;
			anchors.frg_seeker_id.frg_smart_city_id = anchors.smart_cities.find((city) => smartcityId === city.id);
		});
		// console.log(this.anchorData);
		this.widgetLoading = false;
	}
}
