import { Component, Input, OnChanges, OnInit } from "@angular/core";
import { DashboardmainService } from "src/app/seeker/challenge-dashboard/dashboardmain.service";
import * as moment from "moment";

@Component({
	selector: "app-overview-pilots",
	templateUrl: "./overview-pilots.component.html",
	styleUrls: ["./overview-pilots.component.css"],
})
export class OverviewPilotsComponent implements OnChanges {
	@Input() anchorId: any;
	queryAll: any = {
		filter: {
			status: "active",
			proposal_stage: "Pilot Ready",
		},
		sort: "created_at DESC",
		pg: {
			limit: 200,
			skip: 0,
		},
	};
	pilotData: any;
	loading: boolean;
	constructor(private _dash: DashboardmainService) {}

	async ngOnChanges() {
		this.loading = true;
		// console.log("Anchor id", this.anchorId);
		if (this.anchorId != undefined) {
			await this.fetchPilots();
		}
		this.loading = false;
	}

	async fetchPilots() {
		this.queryAll.filter.frg_anchor_id = this.anchorId;
		this.pilotData = await this._dash.getProposals(this.queryAll);
		await this.pilotData.data.map((app) => {
			app.daysago = moment(app.created_at).fromNow();
		});
	}
}
