import { Component, OnInit } from "@angular/core";
import { LoggedInUserState, LoggedInUserStateModel } from "src/app/state/loggedinuser.state";
import { LoggedInUser } from "src/app/state/model/loggedinuser.model";
import { Select } from "@ngxs/store";
import { Observable } from "rxjs";
import { ActivatedRoute } from "@angular/router";
import { ToastrService } from "ngx-toastr";
import { ChallengeService } from "src/app/challenge/challenge.service";
@Component({
	selector: "app-challenge-overview",
	templateUrl: "./challenge-overview.component.html",
	styleUrls: ["./challenge-overview.component.css"],
})
export class ChallengeOverviewComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	currentUser: LoggedInUser;
	overviewLoading: boolean = true;
	anchorId: any;
	challengeId: any;
	anchorData: any;
	challengeData: any;
	constructor(private toastr: ToastrService, private route: ActivatedRoute, private _anchor: ChallengeService) {
		this.route.parent.params.subscribe((params) => (this.anchorId = params.id));
	}

	async ngOnInit() {
		await this.getAnchor();
	}

	async getAnchor() {
		try {
			this.overviewLoading = true;
			this.anchorData = await this._anchor.getAnchorById(this.anchorId);
			// this.isAuthor = this.anchorData.data.frg_seeker_id.id == this.currentUser.userTypeId ? true : false;
			this.challengeData = await this._anchor.getChallengeById(this.anchorData.data.frg_challenge_id.id);
			// console.log("CD", this.challengeData);
			this.overviewLoading = false;
		} catch (err) {
			console.log(err);
		}
	}
}
