import { Component, Input, OnChanges, OnInit } from "@angular/core";
import { ApplicationService } from "../../../review-applications/application.service";
import { environment } from "../../../../../../../environments/environment";
import * as moment from "moment";
@Component({
	selector: "app-anchor-applications",
	templateUrl: "./anchor-applications.component.html",
	styleUrls: ["./anchor-applications.component.css"],
})
export class AnchorApplicationsComponent implements OnChanges {
	serverUrl: string = environment.server_url;
	@Input() anchorId: any;
	query: any = {
		filter: {
			status: "active",
		},
		sort: "created_at DESC",
		pg: {
			limit: 5,
			skip: 0,
		},
	};
	applicationData: any;
	loading: boolean;
	constructor(private _app: ApplicationService) {}

	async ngOnChanges() {
		this.loading = true;
		// console.log("Anchor id", this.anchorId);
		if (this.anchorId != undefined) {
			await this.fetchApplications();
		}
		this.loading = false;
	}
	async fetchApplications() {
		this.query.filter.frg_anchor_id = this.anchorId;
		this.applicationData = await this._app.getApplications(this.query);
		// console.log("Anchor Application", this.applicationData);
		await this.applicationData.data.map((app) => {
			app.daysago = moment(app.created_at).fromNow();
		});
	}
}
