import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { ChallengeDashboardComponent } from "./challenge-dashboard.component";
import { ReviewApplicationsComponent } from "./screens/review-applications/review-applications.component";
import { SendEoiComponent } from "./screens/send-eoi/send-eoi.component";
import { ReviewProposalComponent } from "./screens/review-proposal/review-proposal.component";
import { PilotsComponent } from "./screens/pilots/pilots.component";
import { ChallengeOverviewComponent } from "./screens/challenge-overview/challenge-overview.component";
import { CreateEoiComponent } from "./screens/create-eoi/create-eoi.component";
import { SolIndividualComponent } from "./screens/sol-individual/sol-individual.component";
const routes: Routes = [
	{
		path: "",
		component: ChallengeDashboardComponent,
		children: [
			{
				path: "",
				redirectTo: "review-applications",
				pathMatch: "full",
			},
			{
				path: "review-applications",
				component: ReviewApplicationsComponent,
			},
			{ path: "review-applications/:id", component: SolIndividualComponent },
			{
				path: "eoi",
				component: SendEoiComponent,
			},
			{
				path: "eoi/send",
				component: CreateEoiComponent,
			},
			{
				path: "eoi/:id",
				component: SolIndividualComponent,
			},
			{
				path: "review-proposals",
				component: ReviewProposalComponent,
			},
			{
				path: "pilots",
				component: PilotsComponent,
			},
			{
				path: "overview",
				component: ChallengeOverviewComponent,
			},
		],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class ChallengeDashboardRoutingModule {}
