import { Component, Input, OnInit, OnChanges } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { ApplicationService } from "../../../review-applications/application.service";

@Component({
	selector: "app-eoi-confirm-modal",
	templateUrl: "./eoi-confirm-modal.component.html",
	styleUrls: ["./eoi-confirm-modal.component.css"],
})
export class EoiConfirmModalComponent implements OnInit, OnChanges {
	@Input() check: any;
	@Input() selectedapp: any;
	@Input() Ids;

	constructor(private route: ActivatedRoute, private router: Router) {}

	ngOnInit() {}
	ngOnChanges() {}
	eventCheck(event, id) {
		this.Ids = [];
		if (event.target.checked) {
			this.selectedapp.map((app) => {
				if (app.id == id) {
					app.isChecked = true;
				}
			});
		} else {
			this.selectedapp.map((app) => {
				if (app.id == id) {
					app.isChecked = false;
				}
			});
		}
		this.selectedapp.map((app) => {
			if (app.isChecked == true) {
				this.Ids.push(app.id);
			}
		});
	}
	getIDs() {
		this.Ids = [];
		this.selectedapp.map((app) => {
			if (app.isChecked == true) {
				this.Ids.push(app.id);
			}
		});
	}
}
