import { Component, OnInit } from "@angular/core";
import * as ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import * as _ from "lodash";
import { Validators, FormGroup, FormBuilder } from "@angular/forms";
import { ToastrService } from "ngx-toastr";
import { Select } from "@ngxs/store";
import { Observable } from "rxjs";
import { LoggedInUserState, LoggedInUserStateModel } from "src/app/state/loggedinuser.state";
import { ApplicationService } from "../review-applications/application.service";
import { DashboardmainService } from "../../dashboardmain.service";
import { LoggedInUser } from "src/app/state/model/loggedinuser.model";
import { ActivatedRoute, Router } from "@angular/router";
import { Location } from "@angular/common";

@Component({
	selector: "app-create-eoi",
	templateUrl: "./create-eoi.component.html",
	styleUrls: ["./create-eoi.component.css"],
})
export class CreateEoiComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	public Editor = ClassicEditor;
	loggedInUser: LoggedInUser;
	// yearArr:any = _.range(1950, new Date().getFullYear() + 1);
	weekArr: any = [1, 2, 3, 4, 5];
	// monthArr: any = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
	monthArr: any = [1, 2, 3, 4];
	pageLoading: boolean = false;
	submitLoading: boolean;
	sendEOIStep1: boolean;
	sendEOIStep2: boolean;
	sendEOIStep3: boolean;
	sendEOIForm: any = {
		Step1: FormGroup,
		Step2: FormGroup,
	};
	isSubmitted: any = {
		Step1: false,
		Step2: false,
		Step3: false,
	};
	challengeAnchorId: any;
	selectedAppIds: any = [];

	constructor(
		private toastr: ToastrService,
		private fb: FormBuilder,
		private _app: ApplicationService,
		private route: ActivatedRoute,
		private router: Router,
		private _location: Location,
		private _dash: DashboardmainService
	) {
		this.route.parent.params.subscribe((params) => (this.challengeAnchorId = params.id));
		this.route.queryParams.subscribe((params) => {
			this.selectedAppIds = params["ids"];
		});
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
		});

		this.Editor.defaultConfig = {
			toolbar: {
				items: ["heading", "|", "bold", "italic", "bulletedList", "numberedList", "link"],
			},
			image: {
				toolbar: ["imageStyle:full", "imageStyle:side", "|", "imageTextAlternative"],
			},
			table: {
				contentToolbar: ["tableColumn", "tableRow", "mergeTableCells"],
			},
			language: "en",
		};
	}

	async ngOnInit() {
		this.pageLoading = true;
		this.sendEOIStep1 = false;
		this.sendEOIStep2 = true;
		this.sendEOIStep3 = true;
		this.sendEOIForm = {
			Step1: this.fb.group({
				goals_of_pilot: ["", [Validators.required, Validators.minLength(100)]],
				expected_outcomes: ["", [Validators.minLength(100)]],
				deployment_details: ["", [Validators.required, Validators.minLength(100)]],
			}),
			Step2: this.fb.group({
				deployment_time_month: ["", [Validators.required]],
				deployment_time_week: ["", [Validators.required]],
				budget_limit: ["", [Validators.required]],
				deployment_constraints: ["", [Validators.required, Validators.minLength(100)]],
			}),
		};
		this.pageLoading = false;
	}

	get formControlStep1() {
		return this.sendEOIForm.Step1.controls;
	}
	get formControlStep2() {
		return this.sendEOIForm.Step2.controls;
	}

	stepFormSubmit(formIndex: string) {
		switch (formIndex) {
			case "Step1": {
				this.isSubmitted.Step1 = true;
				this.ShowNextStep(formIndex);
				break;
			}
			case "Step2": {
				this.isSubmitted.Step2 = true;
				this.ShowNextStep(formIndex);
				break;
			}
			case "Step3": {
				this.isSubmitted.Step3 = true;
				this.ShowNextStep(formIndex);
				break;
			}
		}
	}
	async ShowNextStep(formIndex: string) {
		if (formIndex == "Step1" && this.sendEOIForm.Step1.valid) {
			this.gotoStep("step2");
			window.scrollTo(0, 0);
		} else if (formIndex == "Step2" && this.sendEOIForm.Step2.valid) {
			// this.gotoStep("step3");
			await this.createEOIRequest();
			window.scrollTo(0, 0);
		} else if (formIndex == "Step3") {
		} else {
			this.toastr.error("", "Please provide the required values to proceed further");
		}
	}
	gotoStep(step: any) {
		switch (step) {
			case "step1": {
				this.sendEOIStep1 = false;
				this.sendEOIStep2 = true;
				this.sendEOIStep3 = true;
				break;
			}
			case "step2": {
				this.sendEOIStep1 = true;
				this.sendEOIStep2 = false;
				this.sendEOIStep3 = true;
				break;
			}
			case "step3": {
				this.sendEOIStep1 = true;
				this.sendEOIStep2 = true;
				this.sendEOIStep3 = false;
				break;
			}
		}
	}
	async createEOIRequest() {
		try {
			if (this.sendEOIForm.Step1.valid && this.sendEOIForm.Step2.valid && this.selectedAppIds.length > 1) {
				await this.selectedAppIds.forEach((selectedId) => {
					this.submitLoading = true;
					let resp: any = this._app.createEOI({
						data: {
							goals_of_pilot: this.sendEOIForm.Step1.value.goals_of_pilot,
							expected_outcomes: this.sendEOIForm.Step1.value.expected_outcomes,
							deployment_details: this.sendEOIForm.Step1.value.deployment_details,
							deployment_time: {
								month: this.sendEOIForm.Step2.value.deployment_time_month,
								week: this.sendEOIForm.Step2.value.deployment_time_week,
							},
							budget_limit: this.sendEOIForm.Step2.value.budget_limit,
							deployment_constraints: this.sendEOIForm.Step2.value.deployment_constraints,
							frg_seeker_id: this.loggedInUser.userTypeId,
							frg_anchor_id: this.challengeAnchorId,
							frg_application_id: selectedId,
						},
					});
				});
				await this.updateChallengeBriefStatus();
				this.submitLoading = false;
				this.toastr.success("Submitted Successfully");
				this._location.back();
			} else if (this.sendEOIForm.Step1.valid && this.sendEOIForm.Step2.valid) {
				this.submitLoading = true;
				let resp: any = await this._app.createEOI({
					data: {
						goals_of_pilot: this.sendEOIForm.Step1.value.goals_of_pilot,
						expected_outcomes: this.sendEOIForm.Step1.value.expected_outcomes,
						deployment_details: this.sendEOIForm.Step1.value.deployment_details,
						deployment_time: {
							month: this.sendEOIForm.Step2.value.deployment_time_month,
							week: this.sendEOIForm.Step2.value.deployment_time_week,
						},
						budget_limit: this.sendEOIForm.Step2.value.budget_limit,
						deployment_constraints: this.sendEOIForm.Step2.value.deployment_constraints,
						frg_seeker_id: this.loggedInUser.userTypeId,
						frg_anchor_id: this.challengeAnchorId,
						frg_application_id: this.selectedAppIds[0],
					},
				});
				await this.updateChallengeBriefStatus();
				this.submitLoading = false;
				this.toastr.success("Submitted Successfully");
				this._location.back();
			} else {
				this.isSubmitted.Step1 = true;
				this.isSubmitted.Step2 = true;
				this.toastr.error("", "Please provide the required values to proceed further");
			}
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}
	resetFormFields() {
		this.sendEOIForm.Step1.reset();
		this.sendEOIForm.Step2.reset();
		this.sendEOIForm.Step1.markAsPristine();
		this.sendEOIForm.Step2.markAsPristine();
	}

	async updateChallengeBriefStatus() {
		await this._dash.updateChallengeBrief({
			slug: {
				id: this.challengeAnchorId,
			},
			data: {
				challenge_status: "Awaiting Proposal",
			},
		});
	}
}
