import { Component, OnInit } from "@angular/core";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../../../state/loggedinuser.state";
import { LoggedInUser } from "../../../../state/model/loggedinuser.model";
import { Observable } from "rxjs";
import { DashboardmainService } from "../../dashboardmain.service";
import { ActivatedRoute } from "@angular/router";

@Component({
	selector: "app-review-proposal",
	templateUrl: "./review-proposal.component.html",
	styleUrls: ["./review-proposal.component.css"],
})
export class ReviewProposalComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;
	allLoading: boolean;
	awaitProposalLoading: boolean;
	prePilotLoading: boolean;
	receivedLoading: boolean;

	allProposals: any;
	awaitProposal: any;
	prePilot: any;
	proposalReceived: any;

	allProposalCount: number = 0;
	prePilotCount: number = 0;
	awaitProposalCount: number = 0;
	proposalReceivedCount: number = 0;

	queryAll: any = {
		filter: {
			status: "active",
			proposal_stage: ["New", "Pre-Pilot"],
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};

	queryPrePilot: any = {
		filter: {
			status: "active",
			proposal_stage: "Pre-Pilot",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};

	queryAwaitProposal: any = {
		filter: {
			status: "active",
			proposal_received: false,
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};

	queryProposalReceived: any = {
		filter: {
			status: "active",
			proposal_stage: "New",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};

	constructor(private _dash: DashboardmainService, private route: ActivatedRoute) {
		this.route.parent.params.subscribe((params) => {
			this.queryAll.filter.frg_anchor_id = params.id;
			this.queryPrePilot.filter.frg_anchor_id = params.id;
			this.queryAwaitProposal.filter.frg_anchor_id = params.id;
			this.queryProposalReceived.filter.frg_anchor_id = params.id;
		});
	}

	async ngOnInit() {
		await Promise.all([this.fetchAllProposals(), this.fetchAwaitingProposal(), this.fetchProposalReceived(), this.fetchPrePilot()]);
	}

	async fetchAllProposals() {
		this.allLoading = true;
		this.allProposals = await this._dash.getProposals(this.queryAll);
		this.allProposalCount = this.allProposals.total_results;
		this.allLoading = false;
	}

	async fetchAwaitingProposal() {
		this.awaitProposalLoading = true;
		this.awaitProposal = await this._dash.getEois(this.queryAwaitProposal);
		this.awaitProposalCount = this.awaitProposal.total_results;
		this.awaitProposalLoading = false;
	}

	async fetchProposalReceived() {
		this.receivedLoading = true;
		this.proposalReceived = await this._dash.getProposals(this.queryProposalReceived);
		this.proposalReceivedCount = this.proposalReceived.total_results;
		this.receivedLoading = false;
	}

	async fetchPrePilot() {
		this.prePilotLoading = true;
		this.prePilot = await this._dash.getProposals(this.queryPrePilot);
		this.prePilotCount = this.prePilot.total_results;
		this.prePilotLoading = false;
	}

	async handleStatusChange() {
		await Promise.all([this.fetchAllProposals(), this.fetchAwaitingProposal(), this.fetchProposalReceived(), this.fetchPrePilot()]);
	}
	handleAllPagination(pageIndex) {
		this.queryAll.pg.skip = (pageIndex - 1) * this.queryAll.pg.limit;
		this.fetchAllProposals()
	}
	handleAwaitingProposal(pageIndex) {
		this.queryAwaitProposal.pg.skip = (pageIndex - 1) * this.queryAwaitProposal.pg.limit;
		this.fetchAwaitingProposal();
	}
	handleProposalReceived(pageIndex) {
		this.queryProposalReceived.pg.skip = (pageIndex - 1) * this.queryProposalReceived.pg.limit;
		this.fetchProposalReceived();
	}
	handlePrePilot(pageIndex) {
		this.queryPrePilot.pg.skip = (pageIndex - 1) * this.queryPrePilot.pg.limit;
		this. fetchPrePilot();
	}
}
