import { Component, OnInit } from "@angular/core";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../../../state/loggedinuser.state";
import { LoggedInUser } from "../../../../state/model/loggedinuser.model";
import { Observable } from "rxjs";
import { DashboardmainService } from "../../dashboardmain.service";
import { ActivatedRoute } from "@angular/router";

@Component({
	selector: "app-pilots",
	templateUrl: "./pilots.component.html",
	styleUrls: ["./pilots.component.css"],
})
export class PilotsComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;
	allLoading: boolean;
	completedLoading: boolean;
	onGoingLoading: boolean;
	yetToStartLoading: boolean;

	allPilots: any;
	completedPilots: any;
	onGoingPilots: any;
	yetToStart: any;

	allPilotsCount: number = 0;
	completedPilotsCount: number = 0;
	onGoingPilotsCount: number = 0;
	yetToStartCount: number = 0;

	queryAll: any = {
		filter: {
			status: "active",
			proposal_stage: "Pilot Ready",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};

	queryCompleted: any = {
		filter: {
			status: "active",
			proposal_stage: "Pilot Ready",
			pilot_status: "Completed",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};

	queryOnGoing: any = {
		filter: {
			status: "active",
			proposal_stage: "Pilot Ready",
			pilot_status: "Ongoing",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};

	queryYetToStart: any = {
		filter: {
			status: "active",
			proposal_stage: "Pilot Ready",
			pilot_status: "Yet to Start",
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};

	constructor(private _dash: DashboardmainService, private route: ActivatedRoute) {
		this.route.parent.params.subscribe((params) => {
			this.queryAll.filter.frg_anchor_id = params.id;
			this.queryCompleted.filter.frg_anchor_id = params.id;
			this.queryOnGoing.filter.frg_anchor_id = params.id;
			this.queryYetToStart.filter.frg_anchor_id = params.id;
		});
	}

	async ngOnInit() {
		await Promise.all([this.fetchAllPilots(), this.fetchCompletedPilots(), this.fetchOnGoingPilots(), this.fetchYetToStart()]);
	}

	async fetchAllPilots() {
		this.allLoading = true;
		this.allPilots = await this._dash.getProposals(this.queryAll);
		// console.log(this.allPilots);
		this.allPilotsCount = this.allPilots.total_results;
		this.allLoading = false;
	}

	async fetchCompletedPilots() {
		this.completedLoading = true;
		this.completedPilots = await this._dash.getProposals(this.queryCompleted);
		this.completedPilotsCount = this.completedPilots.total_results;
		this.completedLoading = false;
	}

	async fetchOnGoingPilots() {
		this.onGoingLoading = true;
		this.onGoingPilots = await this._dash.getProposals(this.queryOnGoing);
		this.onGoingPilotsCount = this.onGoingPilots.total_results;
		this.onGoingLoading = false;
	}

	async fetchYetToStart() {
		this.yetToStartLoading = true;
		this.yetToStart = await this._dash.getProposals(this.queryYetToStart);
		this.yetToStartCount = this.yetToStart.total_results;
		this.yetToStartLoading = false;
	}

	async handleStatusChange() {
		await Promise.all([this.fetchAllPilots(), this.fetchCompletedPilots(), this.fetchOnGoingPilots(), this.fetchYetToStart()]);
	}
	handleAllPagination(pageIndex) {
		this.queryAll.pg.skip = (pageIndex - 1) * this.queryAll.pg.limit;
		this.fetchAllPilots()
	}
	handleCompletedPilots(pageIndex) {
		this.queryCompleted.pg.skip = (pageIndex - 1) * this.queryCompleted.pg.limit;
		this.fetchCompletedPilots();
	}
	handleOnGoingPilots(pageIndex) {
		this.queryOnGoing.pg.skip = (pageIndex - 1) * this.queryOnGoing.pg.limit;
		this.fetchOnGoingPilots();
	}
	handleQueryYetToStart(pageIndex) {
		this.queryYetToStart.pg.skip = (pageIndex - 1) * this.queryYetToStart.pg.limit;
		this.fetchYetToStart();
	}
}
