import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { filter } from "lodash";
import { ApplicationService } from "../review-applications/application.service";
import { environment } from "../../../../../environments/environment";
import { ToastrService } from "ngx-toastr";
import { connectableObservableDescriptor } from "rxjs/internal/observable/ConnectableObservable";
@Component({
	selector: "app-send-eoi",
	templateUrl: "./send-eoi.component.html",
	styleUrls: ["./send-eoi.component.css"],
})
export class SendEoiComponent implements OnInit {
	serverUrl: string = environment.server_url;
	shortlist: any = [];
	isLoading: boolean;
	buttonDisabled: boolean;
	statusUpdating: boolean;
	challengeAnchorId: any;
	applicationIds: any = [];
	applicationDataShortlisted: any;
	shortlistedCount: number = 0;
	selectedApplication: any = [];
	queryShortlisted: any = {
		filter: {
			status: "active",
			application_status: "Shortlist",
			eoi_received: false,
		},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	constructor(private _app: ApplicationService, private route: ActivatedRoute, private toastr: ToastrService) {
		this.route.parent.params.subscribe((params) => (this.challengeAnchorId = params.id));
		this.queryShortlisted.filter.frg_anchor_id = this.challengeAnchorId;
	}

	ngOnInit() {
		this.buttonDisabled = false;
		this.fetchShortlisted();
	}
	async fetchShortlisted() {
		this.isLoading = true;
		this.applicationDataShortlisted = await this._app.getApplications(this.queryShortlisted);
		this.applicationDataShortlisted.data.map((application) => {
			application.isChecked = false;
		});
		this.shortlistedCount = this.applicationDataShortlisted.total_results;
		// console.log(this.applicationDataShortlisted);
		this.isLoading = false;
	}
	async handleChangeStatus(statusName) {
		let status = statusName.target.value;
		this.statusUpdating = true;
		let resp: any = await this._app.updateApplicationStatus(this.applicationDataShortlisted.id, status);
		this.toastr.info("", `Application moved to ${status}`);
		this.statusUpdating = false;
	}
	async eventCheck(event, id) {
		this.applicationIds = [];
		if (event.target.checked) {
			this.buttonDisabled = true;
			this.applicationDataShortlisted.data.map((app) => {
				if (app.id == id) {
					app.isChecked = true;
				}
			});
		} else {
			this.applicationDataShortlisted.data.map((app) => {
				if (app.id == id) {
					app.isChecked = false;
				}
			});
		}
		this.selectedApplication = this.applicationDataShortlisted.data.filter((app) => {
			return app.isChecked == true;
		});

		this.selectedApplication.map((element) => {
			if (element.isChecked == true) {
				this.applicationIds.push(element.id);
			}
		});
		if (this.applicationIds.length) this.buttonDisabled = true;
		else this.buttonDisabled = false;
	}
	handlePagination(pageIndex) {
		this.queryShortlisted.pg.skip = (pageIndex - 1) * this.queryShortlisted.pg.limit;
		this.fetchShortlisted();
	}
}
