import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from "@angular/core";
import { environment } from "../../../../../../../environments/environment";
import { DashboardmainService } from "../../../../dashboardmain.service";
@Component({
	selector: "pilot-order-upload",
	templateUrl: "./pilot-order-upload.component.html",
	styleUrls: ["./pilot-order-upload.component.css"],
})
export class PilotOrderUploadComponent implements OnChanges {
	@Input() pilotId: any;
	@Input() docs: any;
	@Output() onOrderUpload = new EventEmitter<any>();
	fileNameArr: string[];
	fileArr: any[] = [];
	serverUrl: string = environment.server_url;
	loading: boolean;
	newFile: File;
	constructor(private _dash: DashboardmainService) {}

	ngOnChanges() {
		// console.log(this.docs);
		if (this.docs) {
			this.fileNameArr = this.docs.map((d) => this.extractFileName(d));
			this.fileArr = this.docs;
			// console.log(this.fileNameArr);
		}
	}

	async uploadFile(uploadedFile: File[]) {
		if (uploadedFile.length) {
			let formData = new FormData();
			formData.append("slug", this.pilotId);
			formData.append("pilot_order", uploadedFile[0]);
			this.loading = true;
			let resp: any;
			if (this.docs) {
				resp = await this._dash.addToPilotOrderUpload(formData);
			} else {
				resp = await this._dash.PilotOrderUpload(formData);
			}
			this.loading = false;
			this.onOrderUpload.emit(resp);
		}
	}

	extractFileName(path: string) {
		let filename = path.replace(/^.*[\\\/]/, "");
		return filename;
	}
}
