import { Component, EventEmitter, Input, OnInit, Output, OnChanges } from "@angular/core";
import { environment } from "../../../../../../../environments/environment";
import { DashboardmainService } from "../../../../dashboardmain.service";
import { ToastrService } from "ngx-toastr";

@Component({
	selector: "app-all-pilots",
	templateUrl: "./all-pilots.component.html",
	styleUrls: ["./all-pilots.component.css"],
})
export class AllPilotsComponent implements OnInit, OnChanges {
	@Input() itemId: any;
	@Input() pilotData: any;
	@Output() onStatusChange = new EventEmitter<any>();
	serverUrl: string = environment.server_url;
	statusUpdating: boolean;
	pilotOrder: string[];
	loading: boolean;
	fileNameArr: string[];
	fileArr: any[] = [];
	reportNameArr: string[];
	reportArr: any[] = [];
	constructor(private _dash: DashboardmainService, private toastr: ToastrService) {}

	ngOnInit() {}

	ngOnChanges() {
		if (this.pilotData.pilot_order) {
			this.fileNameArr = this.pilotData.pilot_order.map((d) => this.extractFileName(d));
			this.fileArr = this.pilotData.pilot_order;
		}
		if (this.pilotData.pilot_report) {
			this.reportNameArr = this.pilotData.pilot_report.map((d) => this.extractFileName(d));
			this.reportArr = this.pilotData.pilot_report;
		}
	}

	async handleChangeStatus(status) {
		this.statusUpdating = true;
		let resp: any = await this._dash.updatePilotStatus(this.pilotData.id, status);
		if (status === "Completed") {
			await this._dash.updateChallengeBrief({
				slug: {
					id: this.pilotData.frg_anchor_id.id,
				},
				data: {
					challenge_status: "Pilot",
				},
			});
		}
		this.toastr.info("", `Pilot moved to ${status}`);
		this.onStatusChange.emit(resp);
		this.statusUpdating = false;
	}

	extractFileName(path: string) {
		let filename = path.replace(/^.*[\\\/]/, "");
		return filename;
	}

	handleOrderUpload(updatedData) {
		// console.log(updatedData);
		this.pilotData.pilot_order = updatedData.data[0].pilot_order;
		this.pilotOrder = updatedData.data[0].pilot_order;
	}

	async uploadFile(uploadedFile: File[]) {
		if (uploadedFile.length) {
			let formData = new FormData();
			formData.append("slug", this.pilotData.id);
			formData.append("pilot_order", uploadedFile[0]);
			this.loading = true;
			let resp: any;
			if (this.pilotData.pilot_order) {
				resp = await this._dash.addToPilotOrderUpload(formData);
			} else {
				resp = await this._dash.PilotOrderUpload(formData);
			}
			this.pilotData.pilot_order = resp.data[0].pilot_order;
			this.fileNameArr = this.pilotData.pilot_order.map((d) => this.extractFileName(d));
			this.fileArr = this.pilotData.pilot_order;
			this.loading = false;
		}
	}

	async removeFile(removeAt: number) {
		if (~removeAt) {
			this.loading = true;
			let resp: any = await this._dash.removeFromPilotOrderUpload({
				slug: { id: this.pilotData.id },
				data: { remove_paths: [this.fileArr[removeAt]] },
			});
			this.pilotData.pilot_order = resp.data[0].pilot_order;
			this.fileNameArr = this.pilotData.pilot_order.map((d) => this.extractFileName(d));
			this.fileArr = this.pilotData.pilot_order;
			this.loading = false;
		}
	}

	async uploadReport(uploadedFile: File[]) {
		if (uploadedFile.length) {
			let formData = new FormData();
			formData.append("slug", this.pilotData.id);
			formData.append("pilot_report", uploadedFile[0]);
			this.loading = true;
			let resp: any;
			if (this.pilotData.pilot_report) {
				resp = await this._dash.addToPilotReportUpload(formData);
			} else {
				resp = await this._dash.PilotReportUpload(formData);
			}
			this.pilotData.pilot_report = resp.data[0].pilot_report;
			this.reportNameArr = this.pilotData.pilot_report.map((d) => this.extractFileName(d));
			this.reportArr = this.pilotData.pilot_report;
			this.loading = false;
		}
	}

	async removeReport(removeAt: number) {
		if (~removeAt) {
			this.loading = true;
			let resp: any = await this._dash.removeFromPilotReportUpload({
				slug: { id: this.pilotData.id },
				data: { remove_paths: [this.reportArr[removeAt]] },
			});
			this.pilotData.pilot_report = resp.data[0].pilot_report;
			this.reportNameArr = this.pilotData.pilot_report.map((d) => this.extractFileName(d));
			this.reportArr = this.pilotData.pilot_report;
			this.loading = false;
		}
	}
}
