import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { DashboardmainService } from "./dashboardmain.service";

@Component({
	selector: "app-challenge-dashboard",
	templateUrl: "./challenge-dashboard.component.html",
	styleUrls: ["./challenge-dashboard.component.css"],
})
export class ChallengeDashboardComponent implements OnInit {
	anchorChallengeId: number;
	challengeData: any;
	isLoading: boolean;
	constructor(private _dash: DashboardmainService, private route: ActivatedRoute) {
		this.route.params.subscribe((params) => (this.anchorChallengeId = params.id));
	}

	ngOnInit() {
		this.fetchAnchorChallenge();
	}

	async fetchAnchorChallenge() {
		try {
			this.isLoading = true;
			this.challengeData = await this._dash.getAnchorChallenge(this.anchorChallengeId);
			this.isLoading = false;
		} catch (err) {}
	}

	handleActivate(e) {
		// this.fetchAnchorChallenge();
	}
}
