import { Component, Input, OnChanges, OnInit } from "@angular/core";
import { ApplicationService } from "../../../review-applications/application.service";
import * as moment from "moment";

@Component({
	selector: "app-anchor-eois",
	templateUrl: "./anchor-eois.component.html",
	styleUrls: ["./anchor-eois.component.css"],
})
export class AnchorEoisComponent implements OnChanges {
	@Input() anchorId: any;
	query: any = {
		filter: {
			status: "active",
		},
		sort: "created_at DESC",
		pg: {
			limit: 5,
			skip: 0,
		},
	};
	eoiData: any;
	loading: boolean;
	constructor(private _app: ApplicationService) {}

	async ngOnChanges() {
		this.loading = true;
		// console.log("Anchor id", this.anchorId);
		if (this.anchorId != undefined) {
			await this.fetchEOIs();
		}
		this.loading = false;
	}
	async fetchEOIs() {
		this.query.filter.frg_anchor_id = this.anchorId;
		this.eoiData = await this._app.getEOI(this.query);
	}

	truncateHTML(text: string): string {
		let charlimit = 20;
		if (!text || text.length <= charlimit) {
			return text;
		}
		let without_html = text.replace(/<(?:.|\n)*?>/gm, "");
		let shortened = without_html.substring(0, charlimit) + "...";
		return shortened;
	}
}
