import { Component, Input, OnInit, Output, EventEmitter } from "@angular/core";
import { ApplicationService } from "../../application.service";
import { environment } from "../../../../../../../environments/environment";
import { ToastrService } from "ngx-toastr";
import { v4 as uuid } from "uuid";
import {Router } from "@angular/router";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../../../../../state/loggedinuser.state";
import { LoggedInUser } from "../../../../../../state/model/loggedinuser.model";
import { Observable } from "rxjs";

declare var $;
@Component({
	selector: "app-review-application-card",
	templateUrl: "./review-application-card.component.html",
	styleUrls: ["./review-application-card.component.css"],
})
export class ReviewApplicationCardComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;

	@Input() applicationData: any;
	@Output() onStatusUpdate = new EventEmitter<any>();
	serverUrl: string = environment.server_url;
	statusUpdating: boolean;
	uid: string = uuid();
	statusValue: string = "";
	rejection_msg: any;
	user_slug: any;
	rev_app_id: any;

	constructor(private _app: ApplicationService, private toastr: ToastrService,private router: Router,private LoggedInUserStateModel:LoggedInUserStateModel) {}

	ngOnInit() {
		if (this.applicationData.application_status != "New") {
			this.statusValue = this.applicationData.application_status;
		}
	}

	async handleChangeStatus(statusName) {
		let status = statusName.target.value;
		if (status === "Onhold") {
			this.statusUpdating = true;
			let resp: any = await this._app.updateApplicationStatus(this.applicationData.id, status);
			this.toastr.info("", `Application moved to ${status}`);
			this.onStatusUpdate.emit(resp);
			this.statusUpdating = false;
		} else if (status === "Shortlist") {
			$(`#shortList_${this.uid}`).modal("show");
		} else if (status === "Reject") {
			$(`#reject_${this.uid}`).modal("show");
		}
	}

	async changeStatus(status: string) {
		this.statusUpdating = true;
		let resp: any = await this._app.updateApplicationStatus(this.applicationData.id, status);
		this.toastr.info("", `Application moved to ${status}`);
		$(`#shortList_${this.uid}`).modal("hide");
		$(`#reject_${this.uid}`).modal("hide");
		this.onStatusUpdate.emit(resp);
		this.statusUpdating = false;
		if(status == 'Reject'){
			var post_data = {
				application_name: this.applicationData.name,
				seeker_id:this.applicationData.frg_anchor_id.frg_seeker_id,
				rejection_msg : this.rejection_msg,
				provider_id : this.applicationData.frg_provider_id.frg_user_id
			}
			let resp: any = await this._app.sendAppRejectionNotification(post_data);
		}
		if(status == 'Shortlist'){
			var id = this.applicationData.frg_anchor_id.id
			this.loggedInUser$.subscribe((resp) => {
				this.loggedInUser = resp.LoggedInUser;
				// console.log(this.loggedInUser)
			});
			this.router.navigateByUrl(`/seeker/user/${this.loggedInUser.slug}/dashboard/challenge/${id}/eoi`);
		}
	}

	refreshCard() {
		if (this.applicationData.application_status != "New") {
			this.statusValue = this.applicationData.application_status;
		} else {
			this.statusValue = "";
		}
	}
	handleChangeStatus2(id){

	}


}
