import { Component, OnInit } from "@angular/core";
import { Location } from "@angular/common";
import { ActivatedRoute, Router } from "@angular/router";
import { DashboardmainService } from "../../dashboardmain.service";
import { environment } from "../../../../../environments/environment";
import { ToastrService } from "ngx-toastr";
import { ApplicationService } from "../review-applications/application.service";

declare var $;
@Component({
	selector: "app-sol-individual",
	templateUrl: "./sol-individual.component.html",
	styleUrls: ["./sol-individual.component.css"],
})
export class SolIndividualComponent implements OnInit {
	isLoading: boolean;
	onHoldLoading: boolean;
	rejectLoading: boolean;
	shortlistLoading: boolean;
	applicationData: any;
	applicationId: any;
	server_url: string = environment.server_url;
	disable_rjt_btn:boolean = true;
	rejection_msg: any;
	constructor(private _sol: DashboardmainService, private route: ActivatedRoute, private location: Location, private router: Router, private toastr: ToastrService,private _app: ApplicationService ) {
		this.route.params.subscribe((params) => {
			this.applicationId = params.id;
		});
	}

	ngOnInit() {
		this.fetchSolutionData();
	}

	goBack() {
		this.location.back();
	}

	async fetchSolutionData() {
		try {
			this.isLoading = true;
			this.applicationData = await this._sol.applicationById(this.applicationId);
			this.isLoading = false;
		} catch (err) {}
	}

	async moveToOnhold() {
		this.onHoldLoading = true;
		await this._sol.updateApplicationStatus(this.applicationData.data.id, "Onhold");
		this.toastr.info("", "Application moved to Onhold");
		$("#shortListModal").modal("hide");
		this.fetchSolutionData();
		this.onHoldLoading = false;
	}

	async rejectApplication() {
		// this.rejectLoading = true;
		// await this._sol.updateApplicationStatus(this.applicationData.data.id, "Reject");
		// this.toastr.info("", "Application moved to Rejected");
		// this.fetchSolutionData();
		// this.rejectLoading = false;
		var post_data = {
			application_name: this.applicationData.data.name,
			seeker_id: this.applicationData.data.frg_anchor_id.frg_seeker_id,
			rejection_msg : this.rejection_msg,
			provider_id : this.applicationData.data.frg_provider_id.frg_user_id,
			user_slug : this.applicationData.data.frg_provider_id.slug
		}
		// console.log(post_data)
		let resp: any = await this._app.sendAppRejectionNotification(post_data);
		$('#rejectProposal').modal('hide');
	}

	async shortListApplication() {
		this.shortlistLoading = true;
		await this._sol.updateApplicationStatus(this.applicationData.data.id, "Shortlist");
		this.toastr.info("", "Application moved to Shortlist");
		$("#shortListModal").modal("hide");
		this.fetchSolutionData();
		this.shortlistLoading = false;
	}

	onSelect(event){
		if ( event.target.checked ) {
			this.disable_rjt_btn = false;
		}
		else{
			this.disable_rjt_btn = true;
		}
	}
}
