import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { environment } from "../../../../../../../environments/environment";
import { DashboardmainService } from "../../../../dashboardmain.service";
import { ToastrService } from "ngx-toastr";
import { v4 as uuid } from "uuid";
import { Router } from "@angular/router";
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from "../../../../../../state/loggedinuser.state";
import { LoggedInUser } from "../../../../../../state/model/loggedinuser.model";
import { Observable } from "rxjs";
declare var $;
@Component({
	selector: "app-review-proposal-all",
	templateUrl: "./review-proposal-all.component.html",
	styleUrls: ["./review-proposal-all.component.css"],
})
export class ReviewProposalAllComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;
	@Input() itemId: any;
	@Input() proposalData: any;
	// @Output() onPrePilot = new EventEmitter<any>();
	@Output() onPilot = new EventEmitter<any>();
	pilotLoading: boolean;
	prePilotLoading: boolean;
	refreshTrigger: boolean;
	statusValue: string = "";
	serverUrl: String = environment.server_url;
	uid: string = uuid();
	loading: boolean;
	fileArr: any;
	fileNameArr: any;
	rejection_msg: string;
	rejectProposalLoading: boolean;
	constructor(private _dash: DashboardmainService, private toastr: ToastrService,private router: Router,private LoggedInUserStateModel:LoggedInUserStateModel) {}

	ngOnInit() {
		if (this.proposalData.proposal_stage === "New") this.statusValue = "";
	}
	ngOnChanges() {
		if (this.proposalData.pilot_iic) {
			this.fileNameArr = this.proposalData.pilot_iic.map((d) => this.extractFileName(d));
			this.fileArr = this.proposalData.pilot_iic;
		}
	}

	extractFileName(path: string) {
		let filename = path.replace(/^.*[\\\/]/, "");
		return filename;
	}

	async updateToPilot() {
		this.pilotLoading = true;
		let resp: any = await this._dash.updateProposalStatus(this.proposalData.id, "Pilot Ready");
		this.toastr.success("", "Proposal moved to Pilot Ready");
		this.pilotLoading = false;
		$(`#proposalModal_${this.uid}`).modal("hide");
		$(`#initiatePilot_${this.uid}`).modal("hide");
		this.onPilot.emit(resp);
		var id =this.proposalData.frg_anchor_id.id;
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
			// console.log(this.loggedInUser)
		});
		this.router.navigateByUrl(`/seeker/user/${this.loggedInUser.slug}/dashboard/challenge/${id}/pilots`);
	}

	async updateToPrePilot() {
		this.prePilotLoading = true;
		let resp: any = await this._dash.updateProposalStatus(this.proposalData.id, "Pre-Pilot");
		this.toastr.success("", "Proposal moved to Pre Pilot");
		this.prePilotLoading = false;
		$(`#proposalModal_${this.uid}`).modal("hide");
		$(`#initiatePrePilot_${this.uid}`).modal("hide");
		this.onPilot.emit(resp);
	}

	updateStatus(e) {
		if (e.target.value === "Pilot Ready") {
			$(`#initiatePilot_${this.uid}`).modal("show");
		}
		if (e.target.value === "Pre-Pilot") {
			$(`#initiatePrePilot_${this.uid}`).modal("show");
		}
		if (e.target.value === "Reject") {
			$(`#rejectProposal_${this.uid}`).modal("show");
		}
	}

	refreshCard() {
		this.statusValue = "";
	}

	async uploadFile(uploadedFile: File[]) {
		if (uploadedFile.length) {
			let formData = new FormData();
			formData.append("slug", this.proposalData.id);
			formData.append("pilot_iic", uploadedFile[0]);
			this.loading = true;
			let resp: any;
			// console.log(uploadedFile[0])
			if (this.proposalData.pilot_icc) {
				resp = await this._dash.addToIICUpload(formData);
			} else {
				resp = await this._dash.PilotIIcUpload(formData);
			}
			this.proposalData.pilot_iic = resp.data[0].pilot_iic;
			this.fileNameArr = this.proposalData.pilot_iic.map((d) => this.extractFileName(d));
			this.fileArr = this.proposalData.pilot_iic;
			this.loading = false;
		}
	}

	async removeFile(removeAt: number) {
		if (~removeAt) {
			this.loading = true;
			let resp: any = await this._dash.removeFromIICUpload({
				slug: { id: this.proposalData.id },
				data: { remove_paths: [this.fileArr[removeAt]] },
			});
			this.proposalData.pilot_iic = resp.data[0].pilot_iic;
			this.fileNameArr = this.proposalData.pilot_iic.map((d) => this.extractFileName(d));
			this.fileArr = this.proposalData.pilot_iic;
			this.loading = false;
		}
	}
	async rejectProposal(){
		this.rejectProposalLoading = true;
		var post_data = {
			proposal_id: this.proposalData.id,
			innovator_id : this.proposalData.frg_provider_id.frg_user_id,
			innovator_name : this.proposalData.frg_eoi_id.frg_application_id.frg_provider_id.fullname,
			proposal_name: this.proposalData.frg_eoi_id.frg_application_id.name,
			rejection_message: this.rejection_msg,
			city_name:this.proposalData.frg_anchor_id.frg_seeker_id.frg_smart_city_id.name,
			seeker_name: this.proposalData.frg_anchor_id.frg_seeker_id.fullname,
			user_slug :this.proposalData.frg_provider_id.slug,
			anchor_id : this.proposalData.frg_anchor_id.id
		}
		let resp: any = await this._dash.rejecProposal(post_data);
		this.toastr.success("", "Proposal Rejected successfully");
		this.rejectProposalLoading = false;
		$(`#reject_${this.uid}`).modal("hide");
	}


}
